export default class StateBuffer {
  private capacity : number;
  private array : Array<any>;
  private currentStateIndex : number;

  constructor(capacity = 5 as number) {
    this.capacity = capacity;
    this.currentStateIndex = 0;
    this.array = [];
  }

  copy = (action: any) => {
    return action.slice();
  };

  addState = (action : any) => {

    if (this.currentStateIndex !== this.array.length) {
      this.array = this.array.slice(0, this.currentStateIndex);
    }

    this.array.push(action);

    if (this.array.length > this.capacity) {
      this.array.shift();
    } else {
      this.currentStateIndex++;
    }
  }

  undo = () => {
    if (this.currentStateIndex === 0) {
      return false;
    }

    this.currentStateIndex--;
    return true;
  }

  redo = () => {
    if (this.currentStateIndex + 1 > this.array.length) {
      return false;
    }

    this.currentStateIndex++;
    return true;
  }

  getState = () => {
    return this.currentStateIndex > 0 ? this.copy(this.array[this.currentStateIndex - 1]) : [];
  }
}



