import * as React from 'react';
import './index.scss';
import * as d3 from 'd3';
import StateBuffer from './state-buffer';

class Point {
  public x : number;
  public y : number;

  constructor(x : number, y : number) {
    this.x = x;
    this.y = y;
  }
}

export default class Svg extends React.Component {
  public points : Array<Point> = [];
  public color : string = "#000000";
  public width : number = 3;

  private svg : any;
  private currentPath : any;
  private isDrawing : boolean = false;
  private stateBuffer : StateBuffer = new StateBuffer();

  componentDidMount() {
    if (this.svg) {
      this.svg
        .attr("stroke", this.color)
        .attr("stroke-width", this.width)
        .attr("fill", "none");
    }
  }

  pointsToString = (points = this.points as any) => {
    return points.length === 0 ? '' :
    `M ${points[0].x} ${points[0].y}
    ${points.length > 1 ? points.slice(1).map((point: any) => `L ${point.x} ${point.y}`).join(' ') : ""}`
  };

  addPointAndSetPath = (e: any) => {
    this.points.push(new Point(e.pageX, e.pageY));
    this.currentPath.attr("d", this.pointsToString());
  };

  onMouseDown = (e : any) => {
    this.isDrawing = true;
    this.currentPath = this.svg.append("path");
    this.points = [];
    this.addPointAndSetPath(e);
  }


  onMouseMove = (e: any) => {
    if (this.isDrawing) {
      this.addPointAndSetPath(e);
    }
  }


  onMouseUp = (e: any) => {
    this.isDrawing = false;
    this.addPointAndSetPath(e);

    const state = this.stateBuffer.getState();

    state.push(this.points);

    this.stateBuffer.addState(state);
  }

  undo = () => {
    const needToReboot = this.stateBuffer.undo();

    if (needToReboot) {
      this.updateSvg();
    }
  }

  redo = () => {
    const needToReboot = this.stateBuffer.redo();

    if (needToReboot) {
      this.updateSvg();
    }
  }

  clear = () => {
    if (this.stateBuffer.getState().length !== 0) {
      this.stateBuffer.addState([]);
      this.updateSvg();
    }
  }

  updateSvg = () => {
    this.svg.selectAll("*").remove();

    const state = this.stateBuffer.getState();

    for (let i = 0; i < state.length; i++) {
      const path = this.svg.append("path");
      path.attr("d", this.pointsToString(state[i]));
    }
  }

  onTouchStart = (e: any) => {
    this.onMouseDown(e.changedTouches[0]);
  }

  onTouchMove = (e: any) => {
    this.onMouseMove(e.changedTouches[0]);
  }

  onTouchEnd = (e: any) => {
    this.onMouseUp(e.changedTouches[0]);
  }

  render() {
    return (
      <React.Fragment>
        <div className="action-buttons">
          <span onClick={this.undo} className="action-buttons__item"></span>
          <span onClick={this.redo} className="action-buttons__item"></span>
          <span onClick={this.clear} className="action-buttons__item"></span>
        </div>
        <svg ref={ref => this.svg = d3.select(ref)} className="main-svg"
          onMouseDown={this.onMouseDown} onTouchStart={this.onTouchStart}
          onMouseMove={this.onMouseMove} onTouchMove={this.onTouchMove}
          onMouseUp={this.onMouseUp} onTouchEnd={this.onTouchEnd}
          xmlns="http://www.w3.org/2000/svg"/>
      </React.Fragment>
    );
  }
}



